


<template>
  <div class="text-center">


    <v-dialog
      v-model="show"
      width="350"
      max-width="350"
      persistent
    >
      <v-card>
        <v-card-title class="text-h5">
          {{value.titulo}}
        </v-card-title>

        <v-card-text class="pre-formatted" v-if="value.preformatted === true">
          {{value.texto}}
        </v-card-text>

         <v-card-text v-else>
          {{value.texto}}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click.stop="cerrar()"
          >
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'showDialog',
  props: {
     value: {
       active: {
         type: Boolean,
         default: false
       },
       titulo: {
         type: String,
         default: ''
       },
       texto: {
         type: String,
         default: ''
       },
       preformatted: {
         type: String,
         default: false
       }
     }
  },
  computed: {
    show: {
      get () {
        return this.value.active
      },
      set (value) {
         this.$emit('input', value.active)
      }
    }
  },
  methods:{
    cerrar(){
      this.value.active = false;
    }
  }
}
</script>



<style>

.pre-formatted {
  white-space: pre;
}

</style>